.Toolbar
{
    height: 56px;
    width:100%;
    display: flex;
    position: fixed;
    top: 0;
    left:0;
    background-color: #703b09;
    justify-content: space-between;
    align-items: center;
    padding:0 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height:100%;
}